@media screen and (max-width: 480px) {
    .nav {
        display: none;
    }
}

@media screen and (min-width: 481px) {
    .nav {
        display: flex;
    }
}

.nav {
    list-style: none;
}

.nav_item {
    margin-right: 20px;
    color: aliceblue;
    text-transform: uppercase;
    text-decoration: none;
    transition: all 250ms linear;
}

.nav_item:hover{
    text-decoration: underline;
    cursor: pointer;
}

.active {
    text-decoration: underline;
}